



























import { Component, Vue, Prop, Watch } from 'vue-property-decorator'
import HelloWorld from '@/components/HelloWorld.vue' // @ is an alias to /src
import wx from 'weixin-js-sdk'

@Component({
  components: {
    HelloWorld
  }
})
export default class Home extends Vue {
  $createDialog : any
  $createToast : any
  // 声明周期钩子
  model = { phone: '', openid: '' }
  fields =[{ type: 'input', modelKey: 'phone', label: '手机号', props: { placeholder: '请输入' }, rules: { required: true, type: 'tel' } }, { type: 'input', modelKey: 'verificationCode', label: '验证码', props: { placeholder: '请输入', type: 'text' }, rules: { required: true } }]
  valid = undefined
  validity = {}
  options = {}
  time = 60 // 验证码等待时间
  sendMsgDisabled = false
  isVerification = false // 是否需要开启验证码
  openid = ''
  jump = '' // 登陆成功跳转页
  mounted () {
    this.openid = this.$route.query.openid
    this.jump = this.$route.query.jump
  }

  @Prop({ default: 'default value' }) readonly propB!: string
  @Watch('$route')
  onRouterChanged () {
    console.log('路由编号')
  }

  // 计算属性
  get computedMsg () {
    return 'computed ' + this.propB
  }
  validateHandler (result : any) {
    this.validity = result.validity
    this.valid = result.valid
    console.log(result)
    console.log('validity', result.validity, result.valid, result.dirty, result.firstInvalidFieldIndex)
  }
  submitHandler (e : any) {
    e.preventDefault()
    if (!this.openid) {
      const toast = this['$createToast']({
        time: 1000,
        txt: '稍后再试...'
      })
      toast.show()
      return
    }
    const toast = this['$createToast']({
      time: 0,
      txt: '绑定中...'
    })
    toast.show()
    let reqData = Object.assign({}, this.model)
    reqData.openid = this.openid
    this.$store.dispatch('bindingOpenid', reqData).then((response) => {
      toast.hide()
      console.log(response)
      if (response.success) {
        let resData = response.data
        if (resData.sendSms) {
          this.isVerification = true
          this.startCountdown()
        } else if (resData.isBind) {
          this.$store.commit('SET_SESSION_ID', this.openid)
          this.$router.replace({ path: this.jump })
        } else {
          this['$createDialog']({
            type: 'alert',
            content: '您的手机号没有绑定房屋，请联系您所在小区物业绑定手机！',
            icon: 'cubeic-wrong'
          }).show()
        }
      } else {
        if (response.type === 'verificationCode') {
          this['$createDialog']({
            type: 'alert',
            content: '验证码错误或者过期',
            icon: 'cubeic-wrong'
          }).show()
        }
      }
    })
  }
  submitClick () {
    return false
  }

  startCountdown () {
    let me = this
    if (me.sendMsgDisabled) {
      return false
    }
    me.sendMsgDisabled = true
    let interval = window.setInterval(function () {
      console.log(me.isVerification, 'me.isVerification')
      if (!me.isVerification || (me.time--) <= 0) {
        me.time = 60
        me.sendMsgDisabled = false
        window.clearInterval(interval)
      }
    }, 1000)
  }
  getBindingInfo () {
    let query = this.$route.query
    if (query.code) {
      const toast = this['$createToast']({
        time: 0,
        txt: '数据获取中...'
      })
      toast.show()
      this.$store.dispatch('getCodeInfo', { code: query.code }).then((response) => {
        if (response.success) {
          if (response.data.openid) {
            this.openid = response.data.openid
          } else if (response.data.sessionId) {
            this.$store.commit('SET_SESSION_ID', response.data.sessionId)
            this.$store.commit('SET_USER_INFO', response.data.userInfo)
            this.$router.replace({ path: '/home' })
          }
        }
        toast.hide()
      })
    } else {
      console.log('不是微信登陆')
    }
  }
  @Watch('model.phone')
  phoneChange () {
    if (this.isVerification) {
      this.isVerification = false
      this.time = 60
    }
  }
}
